
     .nav-wrapper{background-color:#333;
     .nav__button--burger{display:none; margin-left:auto; background-color: transparent; border:0;color:white;}
.nav{background-color:#333;display:flex;position: relative;
     &__link{padding:10px;display: inline-block; color:white;}
     &__link:hover{background-color:red;}
}
.dropdown{
     &__menu{display:none;position:absolute;left:0;width:100%; background-color:red;}
     &__heading{color:white;padding:15px;}
     &__content{display:flex; background-color:grey; padding:0 15px;
          li{list-style:none;margin-left:10px;margin-bottom:15px;}
          ul{width:33.33%; padding:0;}
     }
}


